import React from "react";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Desktop from "../../res/background/computer_back.webp";
import Mobile from "../../res/background/mobile_back.webp";
import { styled } from "@mui/material/styles";
import { Typography } from "@mui/material";


const Root = styled(Box)(({ theme }) => ({
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    backgroundAttachment: "fixed",
    color: "white",
    textAlign: "center",
    mt: { xs: 6, md: 2 },
    [theme.breakpoints.down("md")]: {
      backgroundImage: `url(${Mobile})`,
      backgroundPosition: "50% 30%",
    },
    [theme.breakpoints.up("md")]: {
      backgroundImage: `url(${Desktop})`,
      backgroundPosition: "50% 30%",
    },
  }));

function lag(){
    return(
        <Root container sx={{ pt: { xs: 5, md: 10 } }}>
            <Grid sm={12} container>
                <Grid xs={12} sx={{ pb: 0, px: 6, pt:43 }}>
                    <Typography variant="h3">
                        Vlkommen till lag-sidan <br/>
                        Hr hittar du all information om era lag aktiviteter under D<span STYLE="font-size:75%">M</span>D veckan                     
                    </Typography>
                    <Typography variant="h4" gutterButtom sx={{pt:6}}>
                        Tordag 7/4
                    </Typography>
                    <Typography variant="h6" gutterButtom>
                        <li>Fulsittning i tv omgngar med inslpp kl:X och kl:Y</li>
                        <li>Kravallen FrD<span STYLE="font-size:75%">M</span>D ft Imenlla</li>
                    </Typography>
                    <Typography variant="h4" gutterBottom>
                        Fredag 8/4
                    </Typography>
                    <Typography variant="h6" gutterBottom>
                        <li>Middlingsrunda 18-21</li>
                        <li>KK-fredagen ft. Kollektiveriet 22-03</li>
                    </Typography>
                    <Typography variant="h4" gutterBottom>
                        Lrdag 9/4
                    </Typography>
                    <Typography variant="h6" gutterBottom sx={{pb:6}}>
                        <li>D<span STYLE="font-size:75%">M</span>D tvlingen! Inslpp och incheckning brjar 09:00 och tvlingen bjar 10:00 <br/> se till att komma i tid</li>
                        <li>Finsittning, frdrinken brjar kl 17:00</li>
                        <li>AfterDart ft Basshunter</li>
                    </Typography>
                    
                </Grid>

            </Grid>

        </Root>
    )

}

export default lag

