import React from 'react';
import Box from '@mui/material/Box'
import Grid from '@mui/material/Grid'
import Typography from '@mui/material/Typography';
import Link from '@mui/material/Link'
import Desktop from "../../res/background/computer_back.webp";
import Mobile from "../../res/background/mobile_back.webp";
import { styled } from "@mui/material/styles";



const Root = styled(Box)(({ theme }) => ({
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    backgroundAttachment: "fixed",
    color: "white",
    textAlign: "center",
    mt: { xs: 6, md: 2 },
    [theme.breakpoints.down("md")]: {
      backgroundImage: `url(${Mobile})`,
      backgroundPosition: "50% 30%",
    },
    [theme.breakpoints.up("md")]: {
      backgroundImage: `url(${Desktop})`,
      backgroundPosition: "50% 30%",
    },
  }));


function Ulag () {
    return(
        <Root>
            <Grid >
                <Typography variant="h2" guttomButtom sx={{mb:6, mx:6, pt:43}}>
                    U-Lag
                </Typography>
                <Typography  variant="h4" guttomButtom sx={{m:3}}>
                    Anmlan fr U-lag r nu stngd.
                </Typography>
                <Typography  variant="h4" guttomButtom sx={{m:3}}>
                    Schemat fr U-lagen r det som fljer nedan:
                </Typography>
                <Typography  variant="h4" guttomButtom sx={{m:3}}>
                Torsdag:
                </Typography>
                <Typography variant="h6" guttomButtom>
                <li>GUKK vid ankomst p resecentrum</li>
                <li>Mottagningslunch</li>
                <li>Torsdagskrken p <Link href="https://goo.gl/maps/tsp8ezQskF9MxDhg8"
                variant='h6'
                underlink="hover"
                >
                Krallen
                </Link></li>
                <li>Fulsittning</li>
                <li>Kravallen(Krav p Overall) FrD<span tyle="font-size:75%">M</span>D p <Link href="https://goo.gl/maps/tsp8ezQskF9MxDhg8"
                variant='h6'
                underlink="hover"
                >
                Krallen
                </Link> </li>
                </Typography>
                <Typography  variant="h4" guttomButtom sx={{m:3}}>
                Fredag:
                </Typography>
                <Typography variant="h6" guttomButtom>
                <li>Buss till badhus och badhus</li>
                <li>U-Lagslunch efter Badhuset</li>
                <li>Vino Tinto</li>
                <li>Eventuell Temasittning (fr de med det stora paketet) </li>
                <li>D<span tyle="font-size:75%">M</span>D-Fredagen ft Kollektiveriet p <Link href="https://goo.gl/maps/NipMggZE7DDpgoti9"
                variant='h6'
                underlink="hover"
                >
                KK
                </Link></li>
                </Typography>
                <Typography  variant="h4" guttomButtom sx={{m:3}}>
                Lrdag:
                </Typography>
                <Typography variant="h6" guttomButtom sx={{pb:4}}>
                <li>D<span tyle="font-size:75%">M</span>D-tvlingen p Krallen incheckning kl 09:00</li>
                <li> AfterDart, vanlig kldkod p <Link href="https://goo.gl/maps/tsp8ezQskF9MxDhg8"
                variant='h6'
                underlink="hover"
                >
                Krallen
                </Link></li>
                </Typography>

            </Grid>
        </Root>

    )
}

export default Ulag