import React from "react";

import dart from "../../res/dart.gif";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import TemaEnligBakgrund from "../../res/background/4x2vepa.jpg";
import TemaEnligBakgrundMobil from "../../res/background/bakgrund_mobil_test.jpg";
import { styled } from "@mui/material/styles";
import Grid from "@mui/material/Grid";
import logo from "../../res/images/D-group.png";

//

const Root = styled(Box)(({ theme }) => ({
  backgroundRepeat: "no-repeat",
  backgroundSize: "cover",
  backgroundAttachment: "fixed",
  color: "white",
  textAlign: "center",
  mt: { xs: 6, md: 2 },
  [theme.breakpoints.down("md")]: {
    backgroundImage: `url(${TemaEnligBakgrundMobil})`,
    backgroundPosition: "50% 10%",
  },
  [theme.breakpoints.up("md")]: {
    backgroundImage: `url(${TemaEnligBakgrund})`,
    backgroundPosition: "50% 30%",
  },
}));

function Rules() {
  return (
    <Root container sx={{ pt: { xs: 1, md: 11 } }} align="left">
      <Grid
        xs={12}
        sx={{
          backgroundColor: "rgba(0,0,0,0.9)",
          borderRadius: "2%",
          paddingY: "5%",
          marginX: "5%",
        }}
      >
        <img alt="logo" src={logo} width={120} height={120} component="div" />
        <Typography
          variant="h2"
          guttomBottom
          sx={{
            color: "white",
            textAlign: "center",
            fontWeight: "bold",
            marginTop: "3%",
          }}
        >
          Regler
        </Typography>
        <Typography
          variant="h6"
          sx={{ color: "white", padding: { xs: 1, md: 12 }, pb: 4 }}
        >
          <li>
            1.Lagen ska sjlva se till att de har pilar. Pilarna ska besiktigas
            av matchdomaren och godknnas innan varje match. <br />
            <br />
          </li>
          <li>
            2. Vilken av de tv motstndarna som ska brja avgrs genom
            middling. Bda tvlanden kastar varsin pil och den som kommer
            nrmast mitten fr brja. Om bda tvlanden trffar Bulls Eye
            (singel eller dubbel) grs en ny middling. Vid tveksamma fall avgr
            matchdomaren utifall om-middling skall ske. <br />
            <br />
          </li>
          <li>
            3. Varje spelare startar med 301 pong. Efter en kastomgng
            subtraheras den uppndda summan frn spelarens tidigare pongsumma.
            En kastomgng bestr av tre pilar. <br />
            <br />
          </li>
          <li>
            4. Vid summering av pong rknas endast de pilar som sitter fast i
            tavlan efter avslutad kastomgng. Pil som studsar, lossnar, missar
            tavlan eller fastnar i andra pilar rknas inte. <br />
            <br />
          </li>
          <li>
            5. De bda kastarna turas om att avverka sina kastomgngar och
            reducerar drmed sina respektive pongsummor. <br />
            <br />
          </li>
          <li>
            6. Ett ben (en delmatch) avgrs med att en av spelarna trffar
            rtt dubbel s att summan blir exakt noll. Detta kallas fr
            utgng. Man kan bara g ut nr man har ntt 50 pong eller lgre.
            Som dubbel rknas den yttre smala delen av pongfltet samt Bulls
            Eye (dubbelbull, 2*25). Utgng fr gras p frsta, andra eller
            tredje pilen i kastomgngen. Fr en spelare 1 eller mindre pong
            kvar utan att ha gtt ut dms denne som tjock och kastomgngen
            avbryts. Spelaren str d kvar p den tidigare pongsumman. <br />
            <br />
          </li>
          <li>
            7. Frn och med den frsta omgngen d en spelare inleder med 50
            pong eller lgre fr han eller hon fem frsk (dvs maximalt 15
            pilar) p sig att g ut genom att trffa en dubbel. Drefter
            tillmpas enkel utgng, varvid man endast har kravet att komma ner
            till exakt noll under kastomgngen. Man blir nu tjock endast om
            man fr mindre n noll pong. Utgng fr gras p frsta, andra
            eller tredje pilen i en kastomgng. <br />
            <br />
          </li>
          <li>
            8. Om en spelare gr ut efter frsta eller andra pilen behver de
            terstende pilarna ej kastas. I vrigt mste alla pilar kastas fr
            att kastomgngen skall rknas som slutfrd. <br />
            <br />
          </li>
          <li>
            9. Spelare ska befinna sig bakom heldragen linje vid kast. Om
            spelare str framfr eller p linjen dms hela kastomgngen ut och
            spelaren str kvar p sin tidigare pong. <br />
            <br />
          </li>
          <li>
            10. Pil som tappas men inte nr fram fr ej kastas om. Tappas pilen
            bakom heldragen linje tillts dock omkast. <br />
            <br />
          </li>
          <li>
            11. Pilar fr inte avlgsnas frn tavlan innan domaren hunnit
            kontrollera pongen samt summerat pongsumman. Om en pil avlgsnas
            fr tidigt dms kastet bort. <br />
            <br />
          </li>
          <li>
            12. Matchdomarens beslut skall respekteras (det finns dock
            mjlighet att verklaga beslut till verDomaren). <br />
            <br />
          </li>
          <li>
            13. Efter att en match pgtt i 60 minuter kommer matchen att
            avbrytas och den som leder i matchen tilldelas segern. Om det str
            1-1 i ben avgrs matchen via middling. <br />
            <br />
          </li>
          <li>
            14. Det r absolut frbjudet att kasta pilar p ngot annat n
            matchtavlan. <br />
            <br />
          </li>
          <li>
            15. Stilpriset bedms och bestms enhlligt av D-Group och
            bedmningen r partisk. Mutor som r hela D-Group tillhanda innan
            tvlingsdagen bedms likvl de som inkommer under DMD. <br />
            <br />
          </li>
          <li>
            16. verDomaren frbehller sig rtten att diskvalificera lag som
            inte fljer reglerna eller p annat stt beter sig illa. <br />
            <br />
          </li>
          <li>
            17. verDomaren frbehller sig rtten att ndra reglerna om denne
            anser detta ndvndigt.
            <br />
            <br />
          </li>
        </Typography>
      </Grid>
    </Root>
  );
}

export default Rules;
